<template>
  <div class="round-box">
    <header class="flex jc-between ai-center">
      <span v-if="isLockingRound" class="tt text flex ai-center">
        <img class="icon" src="../../images/option/play.png" alt="">{{ language.round_waiting }}
        <span class="time-range">{{ timeRange || ': - :' }}</span>
      </span>
      <span v-else class="tt flex ai-center">
        <img class="icon" src="../../images/option/disable.png" alt="">{{ language.round_ended }}
      </span>
      <span class="no"># {{ convertInfo.epoch || '' }}</span>
    </header>
    <div class="main-con flex flex-column jc-center">
      <div v-if="isLockingRound">
        <div class="progress-bar">
          <i class="animate-inout hdr-el-blue" :style="{width:(1-count/countLength)*100+'%'}"></i>
        </div>
        <p class="counter flex jc-between ai-center text">
          <span>{{ countLengthDisplay }}</span>
          <span>{{ countDisplay }}</span>
        </p>
      </div>
      <div class="content flex flex-column jc-center ai-center">
        <div class="arrow-large ta-center up" :class="convertInfo.delta.gt(0)?'green':'gray'">
          <b class="text">{{ language.btn_up }}<br>{{ convertInfo.bullRate.toRate() }}x {{ language.profit }}</b>
        </div>
        <div class="main border-gray hdr-el-gray"
             :class="positionColor&&`border-${positionColor} hdr-el-${positionColor}`">
          <p class="flex jc-between ai-center">
            <span class="fs-12">{{ isLockingRound ? language.latest_price : language.end_price }}($)</span>
            <span class="up-down-icon">
              <img v-if="convertInfo.delta.gt(0)" src="../../images/option/up.png" alt="">
              <img v-if="convertInfo.delta.lt(0)" src="../../images/option/down.png" alt="">
            </span>
          </p>
          <p class="flex jc-between ai-center text" :class="positionColor">
            <b class="end-price">{{ convertInfo.closePrice?.myFixed() || '' }}</b>
            <span>{{ convertInfo.delta?.myFixed() || '' }}</span>
          </p>
          <p class="p3 flex jc-between ai-center">
            <span>
              <span class="fs-12">{{ language.lock_price }}($)</span><br>
              <span class="text">{{ convertInfo.lockPrice?.myFixed() || '' }}</span>
            </span>
            <span class="ta-right">
              <span class="fs-12">
                {{ language.pool }}
                   <span v-if="contractName=='DcionPricePrediction'">DCoin</span>
                   <span v-else>(USDT)</span> 
                </span>
                <br>
              <span class="text">{{ convertInfo.totalAmount?.myFixed() }}</span>
            </span>
          </p>
        </div>
        <div class="arrow-large ta-center down" :class="convertInfo.delta.lt(0)?'red':'gray'">
          <b class="text">{{ convertInfo.bearRate.toRate() }}x {{ language.profit }}<br>{{ language.btn_down }}</b>
        </div>
      </div>
    </div>
    <footer class="flex jc-center ai-center">
      <span v-if="betAmount.isZero()">{{ language.no_betting }}</span>
      <template v-else>
        <template v-if="!isLockingRound">
          <img v-if="isWinner" src="../../images/option/jiangbei.png" alt="">
          <img v-else src="../../images/option/NFT@3x.png" alt="">
        </template>
        <span class="bet-num" :class="betType==='0'?'green':betType==='1'?'red':'text'">
          {{ betAmount.myFixed() }}  
           <span v-if="contractName=='DcionPricePrediction'">DCoin</span>
           <span v-else>(USDT)</span> 
        </span>
        <img v-show="betType==='0'" src="../../images/option/up.png" alt="">
        <img v-show="betType==='1'" src="../../images/option/down.png" alt="">
      </template>
    </footer>
    <div v-if="isLockingRound&&isCalculating" class="mask flex jc-center ai-center">
      <span class="ta-center">
        <i class="icon-loading"></i><br>{{ currentEpoch ? language.tip_clearing : language.tip_loading }}...
      </span>
    </div>
    <div v-if="!isLockingRound" class="mask"></div>
  </div>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import {BN, counter} from "@/common/util";

export default {
  name: "roundBox",
  props: {
    epochOffset: {type: Number, default: 1},
  },
  data() {
    return {
      betAmount: new BN(0),
      betType: '',
      timer: null,
      info: {},
      timeRange: ''
    }
  },
  watch: {
    currentBlock() {
      this.getTimeRange();
    },
    epoch() {
      this.timeRange = '';
      this.getRoundInfo();
      this.getBetInfoInRound();
    },
    address() {
      this.getBetInfoInRound();
    }
  },
  computed: {
    ...mapState('option', ['currentPrice', 'symbol', 'contractName', 'currentBlock', 'resolution']),
    ...mapState('app', ['address']),
    ...mapGetters(['currentEpoch', 'currentRound', 'language']),
    isCalculating() {
      return this.currentRound.isCalculating;
    },
    countLength() {
      return this.resolution * 60;
    },
    countLengthDisplay() {
      return counter.format(this.countLength);
    },
    count() {
      return this.currentRound.count;
    },
    countDisplay() {
      return this.currentRound.countDisplay;
    },
    epoch() {
      return this.currentEpoch ? new BN(this.currentEpoch).minus(this.epochOffset).toString() : '';
    },
    isLockingRound() {
      return this.epochOffset === 1;
    },
    isWinner() {
      let {betAmount, betType, convertInfo} = this;
      let {delta} = convertInfo;
      return delta && betAmount.gt(0) && ((delta.gt(0) && betType === '0') || (delta.lt(0) && betType === '1'))
    },
    convertInfo() {
      let {info, isLockingRound, currentPrice, symbol} = this;
      let {bearAmount, bullAmount, totalAmount, closePrice, lockPrice, endBlock, lockBlock} = info;
      totalAmount = new BN(totalAmount || 0).toDisplay('balance');
      bullAmount = new BN(bullAmount || 0).toDisplay('balance');
      bearAmount = new BN(bearAmount || 0).toDisplay('balance');
      lockPrice = new BN(lockPrice || 0).toDisplay();
      closePrice = isLockingRound ? currentPrice[symbol] : new BN(closePrice || 0).toDisplay();
      lockBlock = new BN(lockBlock || 0);
      endBlock = new BN(endBlock || 0);
      let bullRate = bullAmount.gt(0) ? totalAmount.div(bullAmount) : new BN(0);
      let bearRate = bearAmount.gt(0) ? totalAmount.div(bearAmount) : new BN(0);
      let delta = closePrice && !closePrice.isZero() && !lockPrice.isZero() ? closePrice.minus(lockPrice) : new BN(0);
      return {
        ...info,
        bearAmount, bullAmount, totalAmount, closePrice, lockPrice, endBlock, lockBlock,
        bullRate, bearRate, delta
      }
    },
    positionColor() {
      let {delta} = this.convertInfo;
      return delta.lt(0) ? 'red' : delta.gt(0) ? 'green' : ''
    },
  },
  methods: {
    getRoundInfo() {
      let {epoch, resolution, contractName} = this;
      epoch && this.$api.getRoundInfo(epoch, resolution, contractName).then(round => {
        this.info = round;
        this.getTimeRange();
      })
    },
    getBetInfoInRound() {
      let {epoch, address, contractName} = this;
      address && epoch && this.$api.getBetInfoInRound(epoch, address, contractName).then((res) => {
        let {amount, position} = res;
        this.betType = position;
        this.betAmount = new BN(amount).toDisplay('balance');
      })
    },
    getTimeRange() {
      let {info, currentBlock, timeRange, countLength} = this;
      let {endBlock} = info;
      if (!endBlock || !currentBlock || timeRange) return;
      let count = counter.calClockCount(endBlock, currentBlock, true);
      let t = new Date().getTime();
      let end = new Date(t - count * 1000);
      let s = end.getSeconds();
      end.setTime(t - count * 1000 + (s > 30 ? 60 - s : -s) * 1000);
      let start = new Date(end - countLength * 1000);
      this.timeRange = start.format('hh:mm') + ' - ' + end.format('hh:mm');
    },
    startTimer() {
      clearInterval(this.timer);
      this.getRoundInfo();
      if (!this.isLockingRound) return;
      this.timer = setInterval(() => {
        if (!['prediction', 'index'].includes(this.$route.name)) return
        this.getRoundInfo();
      }, 1000);
    },
  },
  beforeMount() {
    this.startTimer();
    this.getBetInfoInRound();
  },
  beforeUnmount(){
      clearInterval(this.timer);// 
      this.timer = null;
   },
}
</script>

