<template>
  <div class="betting-box">
    <header class="flex jc-between ai-center">
      <span class="tt text flex ai-center">
        <img class="icon" src="../../images/option/shalou.png" alt="">
        <span>{{ language.round_next }}</span>
        <span class="mini-show" style="margin-left: 15px;">{{ currentRound.timeRange || '??:?? - ??:??' }}</span>
      </span>
      <span class="no"># {{ currentEpoch || '???' }}</span>
    </header>
    <p class="time-range mini-hide">{{ currentRound.timeRange || ': - :' }}</p>
    <div class="arrow-large ta-center down bg-gray">
      <div class="inner">
        {{ language.pool }}
        <p class="text flex jc-center ai-center">
          <img class="icon-s" :src="coinMap[payCoinDcoin]" alt=""   v-if="contractName == 'DcionPricePrediction'">
          <img class="icon-s" :src="coinMap[payCoin]" alt=""   v-else>
          <b class="pool">{{ currentRound.totalAmount?.myFixed() }}</b>
        </p>
        <b class="counter text fs-18">{{ countDisplay }}</b>
      </div>
    </div>
    <ul class="num-box flex jc-between">
      <li class="w45p">
        <p class="tt flex jc-between ai-center">
          <span class="text">{{ language.btn_up }}</span>
        
          <span>{{ language.wallet_balance }}
                <span class="text" v-if="contractName == 'DcionPricePrediction'">{{ fixedFn(dcoin)  }}</span>
                <span class="text" v-else>{{fixedFn(balanceDisplay)}}</span>
          </span>
        </p>
        <div class="amount-bar">
          <label>
            <input type="number" v-model="buyAmountDisplay" @input="amountInput" :placeholder="language.tip_2_3">
            <span class="suffix text d-ib" v-if="contractName == 'DcionPricePrediction'">{{ payCoinDcoin  }}</span>
            <span class="suffix text d-ib" v-else>{{ payCoin }}</span>
          </label>
          <div class="flex">
            <div class="btn flex1" @click="setByPercent(btn.percent)"
                 v-for="(btn,i) in btns" :key="i">
              {{ btn.percent * 100 }}%
            </div>
          </div>
        </div>
      </li>
      <li class="w45p mini-hide">
        <p class="tt flex jc-between ai-center">
          <span class="text">{{ language.btn_down }}</span>
          <span>{{ language.wallet_balance }}
              <span class="text" v-if="contractName == 'DcionPricePrediction'">{{fixedFn(dcoin)   }}</span>
              <span class="text" v-else>{{ fixedFn (balanceDisplay) }}</span>
           </span>
        </p>
        <div class="amount-bar">
          <label>
            <input type="number" v-model="buyAmountDisplay" @input="amountInput" :placeholder="language.tip_2_3">
             <span class="suffix text d-ib" v-if="contractName == 'DcionPricePrediction'">{{ payCoinDcoin  }}</span>
            <span class="suffix text d-ib" v-else>{{ payCoin }}</span>
          </label>
          <div class="flex">
            <div class="btn flex1" @click="setByPercent(btn.percent)"
                 v-for="(btn,i) in btns" :key="i">
              {{ btn.percent * 100 }}%
            </div>
          </div>
        </div>
      </li>
    </ul>
    <div class="bottom-bar flex jc-between">
      <div class="w45p">
        <m-button v-if="!isAuthorized" class="bet-btn hdr-el-blue" @click="doApprove" :loading="isBetting">
          <span class="text">{{ language.btn_authorize }}</span>
        </m-button>
        <m-button v-else-if="betDisabled" class="bet-btn border-gray hdr-el-gray" disabled>
          <img src="../../images/option/disable1.png" alt=""/>
          <span class="gray">{{ language.btn_betted }}</span>
        </m-button>
        <m-button v-else class="bet-btn border-green hdr-el-green" :loading="isBetting"
                  @click="bet('betBull')" :disabled="buyAmount.isZero()"
        >
          <img src="../../images/option/look-up.png" alt=""/>
          <span class="green">{{ language.btn_up }}</span>
        </m-button>
        <p class="flex jc-between">
          <span>{{ language.tip_current_round }}</span>
          <span class="rate text">{{ currentRound.bullRate?.toRate()||2 }}x {{ language.profit }}</span>
        </p>
      </div>

      <div class="w45p ta-center">
        <m-button v-if="!isAuthorized" class="bet-btn hdr-el-blue" @click="doApprove" :loading="isBetting">
          <span class="text">{{ language.btn_authorize }}</span>
        </m-button>

        <m-button v-else-if="betDisabled" class="bet-btn border-gray hdr-el-gray" disabled>
          <img src="../../images/option/disable1.png" alt=""/>
          <span class="gray">{{ language.btn_betted }}</span>
        </m-button>

        <m-button v-else class="bet-btn border-red hdr-el-red" :loading="isBetting"
                  @click="bet('betBear')" :disabled="buyAmount.isZero()"
        >
          <img src="../../images/option/look-down.png" alt=""/>
          <span class="red">{{ language.btn_down }}</span>
        </m-button>

        <p class="flex jc-between">
          <span>{{ language.tip_current_round }}</span>
          <span class="rate text">{{ currentRound.bearRate?.toRate()||2 }}x {{ language.profit }}</span>
        </p>
      </div>

    </div>
    <p class="tip ta-center">{{ language.tip_notice + language.tip_betting }}</p>
    <div v-show="isCalculating" class="mask flex jc-center ai-center">
      <span class="ta-center">
        <i class="icon-loading"></i><br>{{ currentEpoch ? language.tip_locking : language.tip_loading }}...
      </span>
    </div>
  </div>
</template>

<script>
import MButton from "@/components/mButton";
import {BN} from "@/common/util";
import {mapState, mapGetters} from 'vuex';
import {coinMap} from "@/common/walletConfig";
import {inputNumberFixer} from "@/common/dataConvertor";

export default {
  name: "bettingBox",
  components: {MButton},
  data() {
    return {
      coinMap,
      payCoin: 'USDT',
      payCoinDcoin:"DCoin",
      buyAmountDisplay: 0.01,
      btns: [
        {percent: 0.25},
        {percent: 0.50},
        {percent: 0.75},
        {percent: 1},
      ],
      isAuthorized: false,
      isBetting: false,
    }
  },
  watch:{
    address(){
       this.getAllowance();
    },
    resolution(){
       this.getAllowance();
    },
    contractName(){
       this.getAllowance();
    }
  },
  computed: {
    ...mapGetters(['currentRound', 'currentEpoch', 'language']),
    ...mapState('option', ['resolution', 'contractName']),
    ...mapState('app', ['address', 'balance']),
    isCalculating() {
      return this.currentRound.isCalculating;
    },
    betDisabled() {
      return this.currentRound.betAmount !== '0';
    },
    countDisplay() {
      return this.currentRound.countDisplay;
    },
    maxAmount(){
      return this.balance.USDT.toDisplay('balance');
    },
    maxAmountDCoin(){
      return this.balance.DCoin.toDisplay('balance');
    },
    dcoin(){
      return this.balance.DCoin.toDisplay('balance');
    },
    balanceDisplay() {
      return this.maxAmount.myFixed('balance');
    },
    buyAmount() {
      let {buyAmountDisplay} = this;
      return buyAmountDisplay ? new BN(buyAmountDisplay).toWei('balance') : new BN(0);
    }
  },
  methods: {
    getAllowance() {
      let {address, resolution, contractName} = this;
      if (!address) return;
      if(contractName == "DcionPricePrediction"){
        this.$api.getOptionAllowanceDCoin(address, resolution, contractName).then(
           num => this.isAuthorized = num !== '0'
        )
        }else{
          this.$api.getOptionAllowance(address, resolution, contractName).then(
          num => this.isAuthorized = num !== '0'
      )
      }
    },
    doApprove(){
      
      let {address, resolution, contractName} = this;
      if (!address) return;
      this.isBetting = true;
      if(contractName == "DcionPricePrediction"){
        this.$api.approveOptionDCoin(resolution, contractName).then(()=>{
        this.isBetting = false;
        this.isAuthorized = true;
      }).catch(()=>this.isBetting = false);
      }else{
        this.$api.approveOption(resolution, contractName).then(()=>{
        this.isBetting = false;
        this.isAuthorized = true;
      }).catch(()=>this.isBetting = false);
      }
    },
    amountInput(e) {
      let {contractName} = this;
      if(contractName == "DcionPricePrediction"){
        let val = e.target.value;
        let max = this.maxAmountDCoin;
        val = Number(val) > max ? max.toString(10) : val;
        this.buyAmountDisplay = inputNumberFixer(val);
      }else{
      let val = e.target.value;
      let max = this.maxAmount;
      val = Number(val) > max ? max.toString(10) : val;
      this.buyAmountDisplay = inputNumberFixer(val);
      }
    },
    setByPercent(percent) {
     let {contractName} = this;
   
      if(contractName == "DcionPricePrediction"){
         this.buyAmountDisplay = this.balance.DCoin.times(percent).toDisplay('balance').myFixed();
      }else{
        this.buyAmountDisplay = this.balance.USDT.times(percent).toDisplay('balance').myFixed();
      }
   },
  
    bet(betType) {
      let from = this.address, amount = this.buyAmount.toString(10);
      if (!from) this.$api.login();
      else {
       this.isBetting = true;
        this.$api.bet(betType, from, amount, this.resolution, this.contractName).then(() => {
          this.isBetting = false;
          this.$api.getBalance();
           this.$toast.open({ 
           message:this.language.automatically,
           position:'top-right',
           duration:6000
           });
        }).catch(() => {
          this.isBetting = false;
        });
      }
    },
    fixedFn(val){
            var number = val;
            number = String(number).replace(/^(.*\..{4}).*$/,"$1");
           return   number = Number(number)
    }
  },
  mounted() {
    this.getAllowance();
  }
}
</script>

